html {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #000000; }

@media (min-width: 1200px) {
  body {
    background-color: #f2f2f2; } }

.wrapper {
  background-color: #eaeef3;
  width: 100%;
  min-height: 100vh; }

.button {
  display: flex;
  padding: 5px;
  color: rgba(0, 0, 0, 0.54);
  background-color: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer; }
  .button:hover, .button:focus {
    background-color: rgba(0, 0, 0, 0.04); }
  .button:active {
    background-color: rgba(0, 0, 0, 0.14); }
  .button--active {
    color: #1976d2; }

.checkbox label {
  position: relative;
  display: block;
  padding-left: 30px;
  cursor: pointer;
  user-select: none; }
  .checkbox label::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    color: rgba(0, 0, 0, 0.6);
    background-image: url("../img/checkbox.svg");
    background-repeat: no-repeat; }

.checkbox input:checked + label::before {
  color: #1976d2;
  background-image: url("../img/checkbox-checked.svg"); }

.site-header {
  margin-bottom: 40px;
  box-shadow: 0 1px 5px 0 #aeaeae; }

.site-header__container {
  display: flex;
  align-items: center;
  max-width: 1280px;
  margin: 0 auto;
  padding: 20px 40px; }
  @media (max-width: 768px) {
    .site-header__container {
      flex-direction: column;
      align-items: stretch; } }

.site-header__heading {
  margin: 0;
  margin-right: 20px;
  font-size: 28px; }
  @media (max-width: 768px) {
    .site-header__heading {
      margin-right: 0;
      margin-bottom: 20px; } }

.site-header__checkbox {
  margin-left: auto; }
  @media (max-width: 768px) {
    .site-header__checkbox {
      margin-left: 0%; } }

.site-main {
  margin: 0 auto;
  padding: 40px;
  padding-top: 0;
  max-width: 1280px; }

.site-main__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-column-gap: 40px;
  grid-row-gap: 40px;
  grid-template-columns: 1fr 1fr 1fr;
  list-style: none; }
  @media (max-width: 1024px) {
    .site-main__list {
      grid-template-columns: 1fr 1fr; } }
  @media (max-width: 768px) {
    .site-main__list {
      grid-template-columns: 1fr; } }

.site-main__item {
  display: flex; }

.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.87);
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12); }

.card__content {
  margin: 0; }

.card__image-wrapper {
  font-size: 0;
  background-color: rgba(0, 0, 0, 0.04); }

.card__image {
  width: 100%;
  height: 300px;
  object-fit: cover; }

.card__text {
  padding: 5px; }

.card__heading {
  font-size: 16px;
  line-height: 18px;
  font-weight: normal; }

.card__controls {
  display: flex;
  flex-wrap: wrap;
  margin-top: auto;
  padding: 5px; }

.card__button--delete {
  margin-left: auto; }
